
import axios from '../assets/http/http'
import url from './url'

axios.defaults.withCredentials = true

export default {
    getData(params) {
        return axios.get('http://103.43.18.46:8080/order', { params: params })
    },
    // 登录
    login(params) {
        return axios.post('http://192.168.3.23:8080/login/', params)
    },
    // 获取前端全部数据
    getAllData(params) {
        return axios.get('http://192.168.3.23:8080/order/', { parmas: params })
    },
    // 获取前端全部数据
    getAllDataP(params) {
        return axios.post('http://192.168.3.23:8080/order/', params)
    },
    // 获取后台数据列表
    getAllDataMan(params) {
        return axios.get('http://192.168.3.23:8080/adminuser/',{params:params})
    },
    // 添加用户
    addUser(params) {
        return axios.post('http://192.168.3.23:8080/adminuser/', params)
    },
    // 修改URL和重置密码/adminuser/{id}/
    changePSWresetPsw(id, params) {
        return axios.put('http://192.168.3.23:8080/adminuser/' + id + '/', params)
    },
    // 获取URL/adminuser/{id}/
    getUrlData(params) {
        return axios.put('http://192.168.3.23:8080/adminuser/' + params + '/')
    },
    // 普通用户查询
    searchPay(params) {
        return axios.post('http://192.168.3.23:8080/order/', params)
    },
    // 详情数据查询
    searchDetail(params) {
        return axios.post('http://192.168.3.23:8080/dayorder/', params)
    },
    // 登录状态下修改密码/users/
    changePsw(params) {
        return axios.post('http://192.168.3.23:8080/users/', params)
    },
}


