<template>
    <div id="managerIndex">
        <div class="manInSear">
            <div>
                <el-input placeholder="请输入用户名" suffix-icon="el-icon-search" v-model="form.search">
                </el-input>
                <button @click="searchData" style="position:relative;left:-50px;background-color:transparent;top:15px;height:40px;border:none;width:50px;"></button>
            </div>

            <el-button style="margin-left:10px;" @click="addcenterDialogVisible = true" type="primary">添加用户</el-button>
        </div>
        <el-table :data="showData" stripe style="width: 100%">
            <el-table-column type="index" width="50">
            </el-table-column>
            <el-table-column prop="username" label="账号">
            </el-table-column>
            <el-table-column prop="display_password" label="密码">
            </el-table-column>
            <el-table-column label="用户权限">
                <template slot-scope="scope">
                    <el-switch v-model="input" active-color="#13ce66" inactive-color="#ff4949">
                    </el-switch>
                </template>
            </el-table-column>
            <el-table-column prop="trade_status" label="启用状态">
                <template slot-scope="scope">
                    <el-switch @change="isUser(scope.row)" v-model="scope.row.is_status" active-value="1" inactive-value="0" active-color="#13ce66" inactive-color="#ff4949">
                    </el-switch>
                </template>
            </el-table-column>
            <el-table-column prop="trade_money" label="URL配置">
                <template slot-scope="scope">
                    <router-link style="color:#606266!important" :to="'/managerChangeURL/'+scope.row.id">
                        <p class="el-icon-edit-outline" style="font-size:26px;"></p>
                    </router-link>
                </template>
            </el-table-column>
            <el-table-column prop="trade_money" label="重置密码">
                <template slot-scope="scope">
                    <el-button @click="resetPswDio(scope.row.id)" style="background-color:transparent!important" type="text">重置</el-button>
                </template>
            </el-table-column>
            <el-table-column prop="last_login_time" label="最近登录记录">
                <template slot-scope="scope">
                    <p>{{scope.row.last_login_time}}</p>
                    <p>{{scope.row.login_ip}}</p>
                </template>
            </el-table-column>
        </el-table>

        <!--重置密码-->
        <el-dialog title="重置密码" :visible.sync="centerDialogVisible" width="400px" center>
            <p style="text-align:center;">是否确认重置平台密码</p>
            <el-input style="width:250px;margin-left:50px;;" v-model="toChangePsw.password" placeholder="请输入内容"></el-input>
            <span slot="footer" class="dialog-footer">
                <el-button type="primary" @click="resetPsw">确 定</el-button>
                <el-button style="background-color:white;" @click="centerDialogVisible = false">取 消</el-button>

            </span>
        </el-dialog>
        <!--添加用户-->
        <div class="addDio">
            <el-dialog title="添加用户" :visible.sync="addcenterDialogVisible" width="400px" center>
                <el-input v-model="addData.username" placeholder="请输入账号"></el-input>
                <div style="display:flex;">
                    <el-input placeholder="请输入内容" v-model="addData.password" class="input-with-select">
                        <el-button style="background-color:transparent" slot="append" icon="el-icon-refresh"></el-button>
                    </el-input>
                    <el-button slot="append" style="color:white;">获取密码</el-button>
                </div>
                <el-input v-model="addData.url" placeholder="请输入URL(选填)"></el-input>
                <el-input v-model="addData.ymt_name" placeholder="请输入该URL下的账号"></el-input>
                <el-input v-model="addData.ymt_pwd" placeholder="请输入该URL下的密码"></el-input>
                <el-alert v-show="isShowAl" title="后三个选项要一起填哦" type="warning">
                </el-alert>
                <span slot="footer" class="dialog-footer">
                    <el-button style="width:300px;" type="primary" @click="addUser">确 定</el-button>
                </span>
            </el-dialog>
        </div>
        <!--分页-->
        <div class="block">
            <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page.sync="pages.currentPage" :page-size="pages.pageSize" layout="prev, pager, next, jumper" :total="pages.total_page">
            </el-pagination>
        </div>
        <!--<el-pagination style="float:right;margin-top:20px;" @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page.sync="page" page-size="10" layout="prev, pager, next, jumper" :total="page">
                </el-pagination>-->
    </div>
</template>

<script>
export default {
    data() {
        return {
            pages: {
                currentPage: 1,
                total_page: 30,
                pageSize: 10,
            },
            currentPage3: 3,
            isShowAl: false,
            toChangePsw: { password: '' },
            resetPswID: '',
            addData: {
                url: '',
                username: '',
                password: '',
                ymt_name: '',
                ymt_pwd: ''
            },
            addcenterDialogVisible: false,
            centerDialogVisible: false,
            input: false,
            input1: '',
            form: {
                search: '',
                page: '1',
            },
            page1: {
                total_page: 1,
            },
            page: {
                total_page: 1,
            },
            showData: []
        }
    },
    created() {
        this.getAllDataMan()
    },
    methods: {
        searchData() {
            console.log('hhhhhhhhhhhhhhh')
            this.getAllDataMan()
        },
        // 
        isUser(row) {
            console.log(row)
            var obj = {};
            obj.is_status = row.is_status;
            this.$api.changePSWresetPsw(row.id, obj).then(res => {
                // this.getAllDataMan()
                console.log('jjjjjjjjjjjjjjjj')
            })
        },
        // 重置密码弹框
        resetPswDio(id) {
            this.resetPswID = id;
            console.log(this.resetPswID)
            this.centerDialogVisible = true;
        },
        // 重置密码
        resetPsw() {
            this.$api.changePSWresetPsw(this.resetPswID, this.toChangePsw).then(res => {
                this.getAllDataMan()
            })
            this.centerDialogVisible = false;

        },
        // 添加用户
        addUser() {

            if (this.addData.url != '') {
                console.log('111111111')
                if (this.addData.ymt_name == '' || this.addData.ymt_pwd == '') {
                    this.isShowAl = true;
                    return;
                }
            }
            if (this.addData.ymt_name != '') {
                console.log('2222222222')
                if (this.addData.url == '' || this.addData.ymt_pwd == '') {
                    this.isShowAl = true;
                    return;
                }
            }
            if (this.addData.ymt_pwd != '') {
                console.log('333333')
                if (this.addData.ymt_name == '' || this.addData.url == '') {
                    this.isShowAl = true;
                    return;
                }
            }
            this.addcenterDialogVisible = false;
            this.$api.addUser(this.addData).then(res => {
                this.getAllDataMan()
            })
        },
        // 获取数据
        getAllDataMan() {
            console.log('ttttttttttt')
            this.$api.getAllDataMan(this.form).then(res => {
                this.showData = res.results
                this.pages.total_page = res.count
            })
        },
        handleSizeChange() { },
        handleCurrentChange() { },
        // getDAta() {
        //     this.$api.getData().then((res) => {
        //         this.tableData = res.data
        //     })
        // },
    }
}
</script>

<style>
#managerIndex .el-input-group__append,
.el-input-group__prepend {
    background-color: transparent;
}

.addDio .el-dialog__body {
    padding: 20px 50px 0 50px;
}

#managerIndex .el-dialog__header {
    border-bottom: 1px solid #dcdfe6;
}

.addDio .el-input {
    margin-bottom: 20px;
}

#managerIndex .manInSear .el-input {
    width: 300px;
}

#managerIndex .manInSear {
    display: flex;
    justify-content: space-between;
    margin-bottom: 20px;
}

#managerIndex .el-table th {
    background-color: #1f2b42!important;
    color: #ebedee;
}



#managerIndex {
    /*min-width:1100px;*/
    padding: 0 4rem;
    margin-top: 100px;
}

#managerIndex .el-button {
    height: 40px;
    background-color: #089ccc;
}
</style>



