<template>
    <div id="allData">
        <el-form ref="form" :model="form" label-width="80px">
            <div></div>
            <DIV style="display:flex;">
                <el-form-item label="交易状态">
                    <el-select v-model="form.region" placeholder="请选择交易状态">
                        <el-option label="全部" value="全部"></el-option>
                        <el-option label="成功" value="成功"></el-option>
                        <el-option label="失败" value="失败"></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="支付方式">
                    <el-select v-model="form.trade_type" placeholder="请选择支付方式">
                        <el-option label="全部" value=""></el-option>
                        <el-option label="微信" value="2"></el-option>
                        <el-option label="支付宝" value="1"></el-option>
                        <!--<el-option label="银联在线" value="beijing"></el-option>
                                                                        <el-option label="QQ钱包" value="beijing"></el-option>-->
                    </el-select>
                </el-form-item>
                <el-form-item label="刷新时间">
                    <el-select @change="Refresh" v-model="count" placeholder="请选择刷新时间">
                        <el-option label="不刷新" value="0"></el-option>
                        <el-option label="10分钟刷新" value="1"></el-option>
                        <el-option label="30分钟刷新" value="2"></el-option>
                        <!--<el-option label="60S刷新" value="3"></el-option>-->
                    </el-select>
                </el-form-item>
                <el-button @click="search" style="margin-left:10px;" type="primary">查询</el-button>
                <!--<el-button type="primary">掉线了？点这里</el-button>-->
                <span class="dataAll">
                    <router-link to="/dataAggredated">数据合计</router-link>
                </span>
            </DIV>
            <!--<el-button style="margin-left:10px;" @click="getDAta()" type="primary">查询</el-button>-->
        </el-form>
        <el-table :data="showData" stripe style="width: 100%">
            <el-table-column prop="c_time" label="创建时间" width="180">
            </el-table-column>
            <el-table-column prop="order_no" label="订单号" width="180">
            </el-table-column>
            <el-table-column prop="trade_type" label="交易方式">
            </el-table-column>
            <el-table-column prop="trade_status" label="交易状态">
            </el-table-column>
            <el-table-column prop="trade_money" label="交易金额">
            </el-table-column>
        </el-table>
        <!--<el-pagination style="float:right;margin-top:20px;" @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page.sync="page.total_page" page-size="16" layout="prev, pager, next, jumper" :total="page.total_page">
        </el-pagination>-->
    </div>
</template>

<script>
export default {
    data() {
        return {
            isCount: '',
            count: '0',
            page: {
                total_page: 1,
            },
            input: '',
            form: {
                region:'全部',
                trade_type: '全部支付方式',
                page: 1,
            },
            showData: []
        }
    },
    created() {
        //    this.Refresh()
        this.search()
    },
    methods: {
        Refresh() {
            // console.log(count)
            switch (Number(this.count)) {
                case 0:
                    clearInterval(this.isCount);
                    break;
                case 1:
                    this.realCount(100000)
                    break;
                case 2:
                    this.realCount(300000)
                    break;
                // case 3:
                //     this.realCount(60000)
                //     break;
                default:
                    return
            }
            // if (count == '不刷新') {
            //     return;
            // }
            // if (count == '10S刷新') {
            //     setInterval(function() { console.log('111'); }, 1000);
            // }
            // if (count == '30S刷新') {
            //     setInterval(function() { console.log('22222'); }, 2000);
            // }
            // if (count == '60S刷新') {
            //     setInterval(function() { console.log('333333'); }, 3000);
            // }
            // this.isCount = 
        },
        realCount(count) {
            clearInterval(this.isCount);
            var that = this;
            this.isCount = setInterval(function() {
                that.$api.getAllDataP(that.form).then(res => {
                    if (res.code == '000000') {
                        that.showData = res.data;
                        that.page.total_page = Number(res.total_page)
                    }

                })
            }, count);
        },
        // 查询
        search() {

            console.log('gggggggggggg')
            this.$api.getAllDataP(this.form).then(res => {
                console.log(res)
                this.showData = res.data;
                console.log('fffffffffffffffff')
                // this.getAllData()
                // if (res.code == '000000') {
                //     this.showData = res.data;
                //     console.log('this.showData')
                //     console.log(res)
                //     this.page.total_page = res.total_page
                // }

            })
        },
        // getAllData() {
        //     this.$api.getAllData().then(res => {
        //         if (res.code == '000000') {
        //             this.showData = res.data;
        //             this.page.total_page = Number(res.total_page)
        //         }

        //     })
        // },
        handleSizeChange(val) {
            this.form.page = val;
            this.search();
        },
        handleCurrentChange(val) {
            this.form.page = val;
            this.search();
        },
        // getDAta() {
        //     this.$api.getData(this.form).then((res) => {
        //         this.showData = res.data;
        //         console.log('this.showData')
        //         console.log(this.showData)

        //     })
        // },
    }
}
</script>

<style>
#allData .el-table th {
    background-color: #1f2b42!important;
    color: #ebedee;
}

#allData .dataAll {
    display: inline-block;
    height: 40px;
    width: 100px;
    line-height: 40px;

    margin-left: 2rem;
}

#allData .dataAll a {
    color: #3ab0d6;
    text-decoration: none;
}

#allData {
    /*min-width:1100px;*/
    padding: 0 4rem;
}

#allData .el-button {
    height: 40px;
}
</style>



