<template>
    <div id="managerChangeURL">
        <el-form ref="form" :model="form" label-width="80px">
            <el-form-item label="URL地址">
                <el-input v-model="showData.url"></el-input>
            </el-form-item>
            <el-form-item label="账号">
                <el-input v-model="showData.ymt_name"></el-input>
            </el-form-item>
            <el-form-item label="密码">
                <el-input v-model="showData.ymt_pwd"></el-input>
            </el-form-item>
            <el-button class="manConChange" @click="centerDialogVisible = true" type="primary">确认修改</el-button>
        </el-form>
        <el-dialog title="" :visible.sync="centerDialogVisible" width="30%" center>
            <p style="text-align:center;font-weight: bolder;font-size:20px;">是否确认修改?</p>
            <span slot="footer" class="dialog-footer">
                <el-button style="background-color: #089ccc!important;" type="primary" @click="changeURL">确 定</el-button>
                <el-button @click="centerDialogVisible = false">取 消</el-button>

            </span>
        </el-dialog>
    </div>
</template>
<script>
export default {
    data() {
        return {
            showData: {},
            centerDialogVisible: false,
            form: {
                name: ''
            },
        }
    },
    created() {
        this.getUrlData()
    },
    methods: {
        // 获取url数据
        getUrlData() {
            this.$api.getUrlData(this.$route.params.id).then(res => {
                this.showData = res
            })
        },
        // 修改
        changeURL() {
            this.$api.changePSWresetPsw(this.$route.params.id,this.showData).then(res => {
                console.log('66666666')
                this.$router.push('/manager')
                // console.log(res)
                // this.showData = res
            })
            this.centerDialogVisible = false;
        },
    }
}
</script>
<style>
#managerChangeURL .el-form {

    width: 550px;
    margin: 0 auto;
    padding-top: 200px;
}

#managerChangeURL .manConChange {
    width: 470px;
    margin-left: 80px;
    background-color: #089ccc!important;
}

#managerChangeURL {
    width: 100%;
    height: 100%;
    /*z-index: -1;*/
    background-image: url('../assets/image/comfirmBack.png');
    background-size: 100% 100%;
}
</style>




