import axios from 'axios'
import qs from 'qs'
import router from '@/router'
import { Message } from 'element-ui'

// axios.defaults.withCredentials = true
// 添加一个请求拦截器
axios.interceptors.request.use(config => {
  checkParams(config.data)
  if (config.method === 'post') {
    config.data = qs.stringify(config.data, { indices: false })
  }
  // if (config.pathVar) {
  //   config.url = config.url + '/' + config.pathVar
  // }
  return config
}, error => {
  return Promise.reject(error)
})

// 添加一个响应拦截器
axios.interceptors.response.use(response => {
  return new Promise((resolve, reject) => {
    console.log(response)
    if (response.status == '200'||response.status == '201'||response.data.code === '100' || response.data.code === '101' || response.data.code === '000000') {
      resolve(response.data)
    } else if (response.data.code === '000100') {
      router.push('/login')
      getIsNull(response.data.msg)
      reject(response.data)
    } else if (response.data.code === '500') {
      reject(response.data)
      getIsNull(response.data.msg)
    } else {
      reject(response.data)
      getIsNull(response.data.msg)
    }
  })
}, (error) => {
  console.log('error.response')
  console.log(error.response)
  getIsNull(error.response.data.msg)
})

const getIsNull = (mes) => {
  if (!mes) {
    Message.error('未知错误')
  } else {
    Message.error(mes)
  }
}
// 检测参数是否为空字符串
const checkParams = (params) => {
  if (typeof params === 'object') {
    for (let item in params) {
      if (params[item] === '') {
        params[item] = undefined
      }
    }
  }
}
export default axios


