import Vue from 'vue'
import Router from 'vue-router'
import Index from '@/components/index'
import login from '../components/login.vue'
import allData from '../components/allData.vue'
import dataAggredated from '../components/dataAggredated.vue'
import manager from '../components/manager.vue'
import managerChangeURL from '../components/managerChangeURL.vue'
import managerIndex from '../components/managerIndex.vue'
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'login',
      component: login
    },
    {
      path: '/Index',
      name: 'Index',
      component: Index,
      children: [
        {
          path: '/',
          name: 'allData',
          component: allData
        },
        {
          path: '/dataAggredated',
          name: 'dataAggredated',
          component: dataAggredated
        },
      ],
    },
    {
      path: '/manager',
      name: 'manager',
      component: manager,
      children: [
        {
          path: '/',
          name: 'managerIndex',
          component: managerIndex
        },
        {
          path: '/managerChangeURL/:id',
          name: 'managerChangeURL',
          component: managerChangeURL
        },
      ],
    },
  ]
})



