<template>
    <div id="dataAggredated">
        <el-form ref="form" :model="form" label-width="80px">
            <DIV style="display:flex;">

                <el-form-item label="支付方式">
                    <el-select v-model="form.trade_type" placeholder="请选择支付方式">
                        <el-option label="所有支付方式" value="shanghai"></el-option>
                        <el-option label="微信" value="2"></el-option>
                        <el-option label="支付宝" value="1"></el-option>
                        <!--<el-option label="银联在线" value="beijing"></el-option>-->
                        <!--<el-option label="QQ钱包" value="beijing"></el-option>-->
                    </el-select>
                </el-form-item>
                <el-form-item label="时间统计">
                    <el-select v-model="form.search_type" placeholder="请选择刷新时间">
                        <el-option label="当日统计" value="0"></el-option>
                        <!--<el-option label="当周统计" value="beijing"></el-option>-->
                        <el-option label="当月统计" value="1"></el-option>
                    </el-select>
                </el-form-item>
                <el-button style="margin-left:10px;" @click="search" type="primary">查询</el-button>
                <span class="dataAll">
                    <router-link to="/index">返回数据详情</router-link>
                </span>
            </DIV>
            <!--<el-button style="margin-left:10px;" @click="getDAta()" type="primary">查询</el-button>-->
        </el-form>
        <el-table :data="showData" stripe style="width: 100%">
            <el-table-column prop="c_time" label="交易日期" width="180">
            </el-table-column>
            <el-table-column prop="trade_money" label="总金额合计" width="180">
            </el-table-column>
            <el-table-column prop="real_money" label="实收金额合计">
            </el-table-column>
            <el-table-column prop="trade_type" label="支付方式">
            </el-table-column>
            <!--<el-table-column prop="trade_money" label="交易金额">
            </el-table-column>-->
        </el-table>
        <!--<el-pagination style="float:right;margin-top:20px;" @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page.sync="page.total_page" page-size="16" layout="prev, pager, next, jumper" :total="page.total_page">
        </el-pagination>-->
    </div>
</template>
<script>
export default {
    data() {
        return {
            input: '',
            form: {
                trade_type: '所有支付方式',
                search_type:'全部',
                page:''
            },
            page:{
                total_page:1
            },
            showData:[]
        }
    },
    created() {
    },
    methods: {
        // 查询
        search(){
             this.$api.searchDetail(this.form).then(res => {
                 console.log(res.data)
                 this.showData = res.data
                // this.getAllDataMan()
            })
        },
        handleSizeChange(val){
            this.form.page = val;
            this.search();
        },
        handleCurrentChange(val){
            this.form.page = val;
            this.search();
        },
        // getDAta() {
        //     this.$api.getData().then((res) => {
        //         this.tableData = res.data
        //     })
        // },
    }
}
</script>
<style>
#dataAggredated .el-table th{
    background-color: #1f2b42!important;
    color: #ebedee;
}

#dataAggredated .dataAll {
    display: inline-block;
    height: 40px;
    width: 100px;
    line-height: 40px;

    margin-left: 2rem;
}

#dataAggredated .dataAll a {
    color: #3ab0d6;
    text-decoration: none;
}

#dataAggredated {
    /*min-width:1100px;*/
    padding: 0 4rem;
}

#dataAggredated .el-button {
    height: 40px;
}
</style>



