<template>
  <div id="index">
    <header>
      <div style="margin-left:3rem;"> <img style="width:35px;height:35px;" src="../assets/image/logo.png" alt="">
        <span style="position:relative;top:-10px;">尊敬的民生用户，欢迎你！</span>
      </div>
      <div style="display:flex;margin-right:3rem">
        <p style="margin-right:1rem;"><img style="width:25px;height:25px;margin-top:10px;" src="../assets/image/header.png" alt=""></p>
        <div style="color:#3ab0d6">
          <p @click="centerDialogVisible = true">修改密码</p>
          <p @click="loginOut">退出</p>
        </div>
      </div>
    </header>
    <router-view></router-view>
    <!--忘记密码-->
    <el-dialog title="修改密码" :visible.sync="centerDialogVisible" width="400px" center>
      <el-form ref="form" :model="form" label-width="80px">
        <el-form-item label="旧密码">
          <el-input v-model="form.old_password" placeholder="请输入旧密码"></el-input>
        </el-form-item>
        <el-form-item label="新密码">
          <el-input v-model="form.password" placeholder="请输入新密码"></el-input>
        </el-form-item>
        <el-form-item label="确认密码">
          <el-input v-model="form.password2" placeholder="确认新密码"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button style="width:200px;" type="primary" @click="changePsw">提 交</el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script>
export default {
  data() {
    return {
      form: {
        old_password: '',
        password: '',
        password2: '',
      },
      centerDialogVisible: false,
    }
  },
  created() {
  },
  methods: {
    loginOut() {
      this.$confirm('您确定要退出该平台吗, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        this.$message.success('退出成功')
        this.$router.push('/')
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '已取消退出登录'
        })
      })
    },
    //  修改密码
    changePsw() {
      this.$api.changePsw(this.form).then(res => {
        console.log('66666666')
      })
      this.centerDialogVisible = false;
    },
  }
}
</script>
<style>
.el-button {
  background-color: #089ccc!important;
  color: #ebedee!important;
}

#index header {
  width: 100%;
  height: 60px;
  display: flex;
  justify-content: space-between;
  background-color: #5e6772;
  align-items: center;
  color: white;
  margin-bottom: 2rem;
  /*padding:0 3rem;*/
}

#index p {
  margin: 0;
  text-align: center;
}

#index {
  height: 100%;
}
</style>



