<template>
    <div id="manager">
        <header>
            <div> <img src="../assets/image/headerMan.png" alt=""> <span style="position:relative;top:-15px;">后台管理</span></div>
            <div class="headerRight">
                <span><router-link to="/">切换账号</router-link></span>
                <span @click="loginOut">退出</span>
            </div>
        </header>
        <router-view></router-view>
    </div>
</template>
<script>
export default {
  data() {
    return {
    }
  },
  methods: {
    loginOut() {
      this.$confirm('您确定要退出该平台吗, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        this.$message.success('退出成功')
        this.$router.push('/')
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '已取消退出登录'
        })
      })
    },
  }
}
</script>
<style>
#manager {
    width: 100%;
    height: 100%;
}
#manager a{
    color: white;
    text-decoration: none;
}
#manager header {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 60px;
    display: flex;
    justify-content: space-between;
    background-color: #5e6772;
    align-items: center;
    color: white;
    padding: 0 3rem;
    z-index: 111;
}

#manager .headerRight span:nth-child(2) {
    margin-left: 30px;
}
</style>




