

<template>
    <div id="login" :style="'backgroundImage:url('+loginBack+')'">
        <div class="loginDio" :style="'backgroundImage:url('+loginDioBack+')'">
            <p class="loginHeT">账户登录</p>
            <img class="loginBotWhi" src="../assets/image/loginBotWhi.png" alt="">
			
            <div class="userName"><input placeholder="请输入账号" v-model="loginForm.username" type="text"></div>
            <div class="password"><input placeholder="请输入密码" v-model="loginForm.password" type="text"></div>
            <div class="remPsw" @click="remPswShow"><img v-show="noremPsw" src="../assets/image/noremPsw.png" alt=""><img v-show="remPsw" src="../assets/image/remPsw.png" alt="">
                <span>记住密码</span>
            </div>
            <div class="loginBtn" @click="submitForm">登 录</div>
            <p class="findPsw" @click="centerDialogVisible = true">
                忘记密码
            </p>
            <!--忘记密码-->
            <el-dialog title="修改密码" :visible.sync="centerDialogVisible" width="400px" center>
                <el-form ref="form" :model="form" label-width="80px">
                    <el-form-item label="旧密码">
                        <el-input v-model="form.name" placeholder="请输入旧密码"></el-input>
                    </el-form-item>
                    <el-form-item label="新密码">
                        <el-input v-model="form.name" placeholder="请输入新密码"></el-input>
                    </el-form-item>
                    <el-form-item label="确认密码">
                        <el-input v-model="form.name" placeholder="确认新密码"></el-input>
                    </el-form-item>
                </el-form>
                <span slot="footer" class="dialog-footer">
                    <el-button style="width:200px;" type="primary" @click="centerDialogVisible = false">提 交</el-button>
                </span>
            </el-dialog>

        </div>
    </div>
</template>   
<script>
import loginDioBack from '../assets/image/loginDioBack.png'
import loginBack from '../assets/image/loginBack.png'
export default {
    data() {
        return {
            loginForm: {
                username: '',
                password: ''
            },
            // rules: {
            //     username: [
            //         { required: true, message: '请输入用户名', trigger: 'blur' }
            //     ],
            //     password: [
            //         { required: true, message: '请输入密码', trigger: 'blur' }
            //     ]
            // },
            form: {
                name: ''
            },
            centerDialogVisible: false,
            noremPsw: true,
            remPsw: false,
            loginBack: loginBack,
            loginDioBack: loginDioBack,
        }
    },
    watch: {
        remPsw: function() {
            if (this.remPsw == true) {
                sessionStorage.setItem("login", JSON.stringify(this.loginForm));
            } else {
                sessionStorage.removeItem("login");
            }
        }
    },
    created() {
        if (sessionStorage.getItem("login")) {
            this.remPsw = true;
            this.noremPsw = false;
            this.loginForm = JSON.parse(sessionStorage.getItem("login"))
        }
    },
    methods: {
        submitForm(formName) {
            // this.$refs[formName].validate((valid) => {
            // if (valid) {
            // let params = {}
            // Object.assign(params, this.ruleForm)
            // params.password = md5(params.password)
            // console.log(params)
            this.$api.login(this.loginForm).then(res => {
                console.log(res)
                if (res.code == '100') {
                    console.log(res)
                    this.$message.success('登录成功')
                    this.$router.push('/manager')
                }
                if (res.code == '101') {
                    console.log(res)
                    this.$message.success('登录成功')
                    this.$router.push('/Index')
                }
                 if (res.code == '1') {
                    console.log(res)
                    this.$message.info('登录失败')
                    // this.$router.push('/Index')
                }
            })
            // } else {
            //     console.log('error submit!!')
            //     return false
            // }
            // })
        },
        remPswShow() {
            this.noremPsw = !this.noremPsw;
            this.remPsw = !this.remPsw;
        },
    }
}

</script>
<style>
#login .el-form input {
    margin-left: 0!important;
}

#login .findPsw {
    width: 270px;
    height: 40px;
    margin-left: 40px;
    text-align: right;
    color: beige;
    text-decoration: underline;
}

#login .loginBtn {
    width: 270px;
    height: 40px;
    background-image: url('../assets/image/loginBtn.png');
    background-size: 100% 100%;
    margin-left: 40px;
    text-align: center;
    color: white;
    line-height: 40px;
}

#login .remPsw {
    margin: 20px 40px;
    color: beige;
    font-size: 16px;
    height: 20px;
    line-height: 20px;
}

#login .remPsw span {
    position: relative;
    top: -5px;
    left: 5px;
}

#login .loginDio {
    width: 350px;
    height: 350px;
}

#login .loginDio input {
    height: 40px;
    width: 235px;
    margin-left: 37px;
    background-color: transparent;
}

#login .loginHeT {
    color: white;
    text-align: center;
    margin: 10px 0 0 0;
}

#login .userName {
    /*margin-left: 30px;*/
    margin: 30px 40px;
    width: 270px;
    height: 40px;
    background-image: url('../assets/image/userName.png');
    background-size: 100% 100%;
}

#login .password {
    margin: 0 40px;
    /*margin-top:30px;*/
    width: 270px;
    height: 40px;
    background-image: url('../assets/image/password.png');
    background-size: 100% 100%;
}

#login .loginBotWhi {
    width: 100%;
}

#login {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100%;
    background-size: 100% 100%;
}
</style>




